.complete {
    text-decoration: line-through;
}

.incomplete {
    text-decoration: none;
}


.add-button{
    padding: 7px 91px;
    margin: 20px auto;
    display: flex;
}
.todo-div {
    margin-bottom: 18px;
}

.msg-input{
    width: 50%;
    margin: 0px auto;
}
@media only screen and (max-width: 768px) {
    .add-button{
        margin-top: 10px;
        padding: 10px 70px;
    }

    .msg-input {
        width: 100%;
    }

    .todo-div {
        margin-bottom: 18px;
        border-bottom: 1px dotted black;
        padding-bottom: 40px;
    }
}